<!-- Lynn -->
<script>
export default {
  name: "accountCensorHistoryDetails-component",
  data() {
    return {
      detail: {}, // 審核明細
      history: [], // 審核狀態
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    async getAccCensorHistoryDetail(id) {
      const res = await this.$store.dispatch("getAccCensorHistoryDetail", id);
      const data = res.data.data;

      // 將明細內容覆寫入 detail, history
      this.detail = data.detail;
      this.history = data.history;
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 打 API 取得該筆明細
    const id = this.$route.params.id;
    this.getAccCensorHistoryDetail(id);
  },
};
</script>

<template src="./accountCensorHistoryDetails.html"></template>
